import * as ActionTypes from '../actions';

const initialState = {
    wallet: [],
    detailWallet: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_WALLET:
            return {
                ...state,
                wallet: action.wallet
            };
        case ActionTypes.STORE_WALLET: {
            const newWallet = [...state.wallet];
            newWallet.splice(0, 0, action.detailWallet);
            return {
                ...state,
                detailWallet: action.detailWallet,
                wallet: newWallet
            };
        }
        case ActionTypes.UPDATE_WALLET: {
            const updateWallet = [...state.wallet];
            updateWallet.splice(action.index, 1);
            updateWallet.splice(action.index, 0, action.detailWallet);
            return {
                ...state,
                detailWallet: action.detailWallet,
                wallet: updateWallet
            };
        }
        case ActionTypes.DELETE_WALLET: {
            const deleteWallet = [...state.wallet];
            deleteWallet.splice(action.index, 1);
            return {
                ...state,
                wallet: deleteWallet
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
