import * as ActionTypes from '../actions';

const initialState = {
    company: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.GET_COMPANY:
            return {
                ...state,
                company: action.company
            };
        case ActionTypes.STORE_COMPANY: {
            let newCompany = {...state.company}
            newCompany = action.company
            return {
                ...state,
                company: newCompany
            };
        }
        case ActionTypes.UPDATE_COMPANY: {
            let newCompany = {...state.company}
            newCompany = action.company
            return {
                ...state,
                company: newCompany
            };
        }
        // case ActionTypes.DELETE_WALLET: {
        //     const deleteWallet = [...state.wallet];
        //     deleteWallet.splice(action.index, 1);
        //     return {
        //         ...state,
        //         wallet: deleteWallet
        //     };
        // }
        default:
            return {...state};
    }
};

export default reducer;
