import * as ActionTypes from '../actions';

const initialState = {
    users: [],
    detailUser: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_USERS: 
            return {
                ...state,
                users: action.users
            };
        case ActionTypes.STORE_USER: {
            const newUser = [...state.users]
            newUser.splice(0, 0, action.detailUser)
            return {
                ...state,
                detailUser: action.detailUser,
                users: newUser
            };
        }
        case ActionTypes.UPDATE_USER: {
            const updateUser = [...state.users]
            updateUser.splice(action.index, 1)
            updateUser.splice(action.index, 0, action.detailUser)
            return {
                ...state,
                detailUser: action.detailUser,
                users: updateUser
            };
        }
        case ActionTypes.DELETE_USER: {
            const deleteUser = [...state.users]
            deleteUser.splice(action.index, 1)
            return {
                ...state,
                users: deleteUser,
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
