import * as ActionTypes from '../actions';

const initialState = {
    products: [],
    detailProduct: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_PRODUCTS:
            return {
                ...state,
                products: action.products
            };
        case ActionTypes.STORE_PRODUCT: {
            const newProduct = [...state.products];
            newProduct.splice(0, 0, action.detailProduct);
            return {
                ...state,
                detailProduct: action.detailProduct,
                products: newProduct
            };
        }
        case ActionTypes.UPDATE_PRODUCT: {
            const updateProduct = [...state.products];
            updateProduct.splice(action.index, 1);
            updateProduct.splice(action.index, 0, action.detailProduct);
            return {
                ...state,
                detailProduct: action.detailProduct,
                products: updateProduct
            };
        }
        case ActionTypes.DELETE_PRODUCT: {
            const deleteProduct = [...state.products];
            deleteProduct.splice(action.index, 1);
            return {
                ...state,
                products: deleteProduct
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
