import {createStore, combineReducers} from 'redux';
import authReducer from './reducers/auth';
import userReducer from './reducers/user';
import productReducer from './reducers/product';
import productCategoryReducer from './reducers/product-category';
import submissionCategoryReducer from './reducers/submission-category';
import walletReducer from './reducers/wallet';
import CompanyReducer from './reducers/company-profile'
import ItemsReducer from './reducers/items'
import SubmissionsReducer from './reducers/submissions'
import TransactionsReducer from './reducers/transactions'

const rootReducer = combineReducers({
    auth: authReducer,
    user: userReducer,
    product: productReducer,
    productCategory: productCategoryReducer,
    submissionCategory: submissionCategoryReducer,
    wallet: walletReducer,
    company: CompanyReducer,
    items: ItemsReducer,
    submissions: SubmissionsReducer,
    transactions: TransactionsReducer
});

const store = createStore(rootReducer);

export default store;
