import * as ActionTypes from '../actions';

const initialState = {
    transactions: [],
    detailWallet: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_TRANSACTIONS:
            return {
                ...state,
                transactions: action.transactions
            };
        default:
            return {...state};
    }
};

export default reducer;
