import * as ActionTypes from '../actions';

const initialState = {
    submissions: [],
    detailSubmission: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_SUBMISSIONS:
            return {
                ...state,
                submissions: action.submissions
            };
        
        case ActionTypes.STORE_SUBMISSIONS: {
            return {
                ...state,
            };
        }
        case ActionTypes.SHOW_SUBMISSIONS:
            return {
                ...state,
                detailSubmission: action.detailSubmission
            };
        case ActionTypes.DELETE_SUBMISSION: {
            const updateSubmission = [...state.submissions];
            updateSubmission.splice(action.index, 1);
            return {
                ...state,
                submissions: updateSubmission
            };
        }
        case ActionTypes.UPDATE_STATUS:{
            const updateSubmission = [...state.submissions]
            updateSubmission.splice(action.index, 1)
            updateSubmission.splice(action.index, 0, action.detailSubmission)
            return {
                ...state,
                detailSubmission: action.detailSubmission,
                submissions: updateSubmission
            };
        }
        case ActionTypes.UPDATE_FULLFILMENT:{
            const updateSubmission = [...state.submissions]
            updateSubmission.splice(action.index, 1)
            updateSubmission.splice(action.index, 0, action.detailSubmission)
            return {
                ...state,
                detailSubmission: action.detailSubmission,
                submissions: updateSubmission
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
