import React, { useEffect } from 'react';
import {Link} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import {connect} from 'react-redux';
import {toast} from 'react-toastify';
import LoadingSpinner from '../../components/loading-spinner/LoadingSpinner'

import * as ProfileService from '../../services/company-profile';
import * as ActionTypes from '../../store/actions';
import Modal from '../../components/Modal';

const Profile = ({getCompany, company, updateCompany, storeCompany}) => {
    const [t] = useTranslation();

    const [loading, setLoading] = React.useState(false);
    const [data, setData] = React.useState({});
    const [show, setShow] = React.useState(false);
    const [showImage, setShowImage] = React.useState(false);
    const [payload, setPayload] = React.useState({
        id: null,
        ownerName: null,
        companyName: null,
        companyAddress: null,
        companyNumber: null,
        companyEmail: null,
        companyTelp: null,
        description: null,
        companyLogo: null
    });
    const [tabIndex, setTabIndex] = React.useState(-1);

    useEffect(() => {
        setLoading(!loading);
            ProfileService.getCompany().then((response) => {
                setLoading(false)
                getCompany(response.data.data);
            })
    }, [])

    useEffect(() => {
        setData(company);
    }, [company]);

    const clearInput = () => {
        setPayload({
            id: null,
            ownerName: null,
            companyName: null,
            companyAddress: null,
            companyNumber: null,
            companyEmail: null,
            companyTelp: null,
            description: null
        });
    };

    const handleClose = () => setShow(false);

    const handleShow = (index) => {
        setShow(true);
        setTabIndex(index);
        if (data) {
            const {createdAt, updatedAt, ...newData} = data;
            setPayload({...newData});
        } else {
            clearInput();
        }
    };

    const handleShowImage = (index) => {
        setShowImage(true);
        setTabIndex(index);
        if (data) {
            const {createdAt, updatedAt, companyLogo, ...newData} = data;
            setPayload({...newData});
        }
    };

    const handleCloseImage = () => setShowImage(false);

    const handleSubmitImage = () => {
        setLoading(!loading);
        const newPayload = new FormData()
        newPayload.append('ownerName', payload.ownerName)
        newPayload.append('companyName', payload.companyName)
        newPayload.append('companyAddress', payload.companyAddress)
        newPayload.append('companyNumber', payload.companyNumber)
        newPayload.append('companyEmail', payload.companyEmail)
        newPayload.append('companyTelp', payload.companyTelp)
        newPayload.append('companyLogo', payload.companyLogo)
        newPayload.append('description', payload.description)
        ProfileService.updateCompany(payload.id, newPayload)
        .then((response) => {
            setTimeout(() => {
                updateCompany(response.data);
                setLoading(false);
                setShowImage(false);
                toast.success('Profil Company Logo Updated!');
            }, 1000);
        })
        .catch((error) => {
            setTimeout(() => {
                setLoading(false);
            }, 1000);
        });
    };

    const handleSubmit = () => {
        setLoading(!loading);
        if (tabIndex === -1) {
            ProfileService.storeCompany(payload)
                .then((response) => {
                    setTimeout(() => {
                        storeCompany(response.data);
                        setLoading(false);
                        setShow(false);
                        toast.success('Profil Company Created!');
                    }, 1000);
                })
                .catch((error) => {
                    setTimeout(() => {
                        setLoading(false);
                    }, 1000);
                });
        } else {
            ProfileService.updateCompany(payload.id, payload)
                .then((response) => {
                    setTimeout(() => {
                        updateCompany(response.data);
                        setLoading(false);
                        setShow(false);
                        toast.success('Profil Company Updated!');
                    }, 1000);
                })
                .catch((error) => {
                    setTimeout(() => {
                        setLoading(false);
                    }, 1000);
                });
        }
    };

    return (
        <>
            <LoadingSpinner 
                isLoading={loading}
            />
            {/* Modal Company Form */}
            <Modal
                customTitle={(
                    <div>
                        <h4
                            style={{
                                fontSize: '1.3rem',
                                color: '#252733',
                                fontWeight: 'bold'
                            }}
                        >
                            {tabIndex === -1 ? 'Atur Company Profile' : 'Edit Company Profile'}
                        </h4>
                    </div>
                  )}
                size="lg"
                show={show}
                onHide={handleClose}
                onClose={handleClose}
                backdrop="static"
                keyboard={false}
                onSubmit={handleSubmit}
                body={(
                    <div className="m-5">
                        <div className="form-group row">
                            <label
                                htmlFor="Owner Name"
                                className="col-sm-3 col-form-label"
                            >
                                Owner Name
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.ownerName}
                                    placeholder="Owner Name"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            ownerName: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="Company Name"
                                className="col-sm-3 col-form-label"
                            >
                                Company Name
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.companyName}
                                    placeholder="Company Name"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyName: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="Company Address"
                                className="col-sm-3 col-form-label"
                            >
                                Company Address
                            </label>
                            <div className="col-sm-9">
                                <textarea
                                    type="text"
                                    className="form-control"
                                    placeholder="Company Address"
                                    defaultValue={payload.companyAddress}
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyAddress: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="NPWP"
                                className="col-sm-3 col-form-label"
                            >
                                NPWP
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.companyNumber}
                                    placeholder="NPWP"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyNumber: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="Company Email"
                                className="col-sm-3 col-form-label"
                            >
                                Company Email
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.companyEmail}
                                    placeholder="Company Email"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyEmail: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="Company Telp"
                                className="col-sm-3 col-form-label"
                            >
                                Company Telp
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.companyTelp}
                                    placeholder="Company Telp"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyTelp: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label
                                htmlFor="Description"
                                className="col-sm-3 col-form-label"
                            >
                                Description
                            </label>
                            <div className="col-sm-9">
                                <textarea
                                    type="text"
                                    className="form-control"
                                    defaultValue={payload.description}
                                    placeholder="Description"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            description: e.target.value
                                        });
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                  )}
                submitTitle="Save"
                closeTitle="Close"
            />
            <Modal
                customTitle={(
                    <div>
                        <h4
                            style={{
                                fontSize: '1.3rem',
                                color: '#252733',
                                fontWeight: 'bold'
                            }}
                        >
                            Ubah Logo Company
                        </h4>
                    </div>
                  )}
                size="lg"
                show={showImage}
                onHide={handleCloseImage}
                onClose={handleCloseImage}
                backdrop="static"
                keyboard={false}
                onSubmit={handleSubmitImage}
                body={(
                    <div className="m-5">
                        <div className="form-group row">
                            <label
                                htmlFor="Owner Name"
                                className="col-sm-3 col-form-label"
                            >
                                Upload File
                            </label>
                            <div className="col-sm-9">
                                <input
                                    type="file"
                                    className="form-control"
                                    defaultValue={payload.companyLogo}
                                    placeholder="Owner Name"
                                    onChange={(e) => {
                                        setPayload({
                                            ...payload,
                                            companyLogo: e.target.files[0]
                                        });
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                  )}
                submitTitle="Save"
                closeTitle="Close"
            />
            <section className="content-header">
                <div className="container-fluid">
                    <div className="row mb-2">
                        <div className="col-sm-6">
                            <h1>{t('header.user.profile')}</h1>
                        </div>
                        <div className="col-sm-6">
                            <ol className="breadcrumb float-sm-right">
                                <li className="breadcrumb-item">
                                    <Link to="/">{t('header.label.home')}</Link>
                                </li>
                                <li className="breadcrumb-item active">
                                    {t('views.user.profile')}
                                </li>
                            </ol>
                        </div>
                    </div>
                </div>
            </section>
            <section className="content">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="card card-primary card-outline">
                                <div className="card-body box-profile">
                                    {data ?
                                    <div>
                                        <button
                                                className="btn btn-success btn-sm"
                                                type="button"
                                                style={{
                                                    background:
                                                        'rgb(17, 78, 96)',
                                                    borderColor:
                                                        'rgb(17, 78, 96)'
                                                }}
                                                onClick={() => handleShow(1)}
                                            >
                                                Update Company Profile
                                        </button>
                                        <div className="text-center">
                                            <img
                                                onClick={() => handleShowImage(1)}
                                                className="profile-user-img img-fluid img-circle"
                                                src={data.companyLogo
                                                    ? process.env.REACT_APP_URL_FOTO_COMPANY +
                                                      data.companyLogo
                                                    : '/img/default-profile.png'}
                                                alt="User profile"
                                            />
                                        </div>
                                        <h3 className="profile-username text-center">
                                            {data.companyName}
                                        </h3>
                                        <p className="text-muted text-center">
                                            {data.description}
                                        </p>
                                        <ul className="list-group list-group-unbordered mb-3">
                                            <li className="list-group-item">
                                                <b>Owner Name</b>

                                                <span className="float-right">
                                                    {data.ownerName}
                                                </span>
                                            </li>
                                            <li className="list-group-item">
                                                <b>Company Address</b>
                                                <span className="float-right">
                                                    {data.companyAddress}
                                                </span>
                                            </li>
                                            <li className="list-group-item">
                                                <b>NPWP</b>
                                                <span className="float-right">
                                                    {data.companyNumber}
                                                </span>
                                            </li>
                                            <li className="list-group-item">
                                                <b>Phone</b>
                                                <span className="float-right">
                                                    {data.companyTelp}
                                                </span>
                                            </li>
                                            <li className="list-group-item">
                                                <b>Email</b>
                                                <span className="float-right">
                                                    {data.companyEmail}
                                                </span>
                                            </li>
                                        </ul>
                                        <small className="float-right mt-3">
                                            Last Updated: Fri Apr 30 2021 16:42:56
                                        </small>
                                    </div> :
                                    <div className="text-center">
                                        <button
                                                className="btn btn-success btn-sm"
                                                type="button"
                                                style={{
                                                    background:
                                                        'rgb(17, 78, 96)',
                                                    borderColor:
                                                        'rgb(17, 78, 96)'
                                                }}
                                                onClick={() => handleShow(-1)}
                                            >
                                                Atur Company Profile
                                        </button>
                                    </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

const mapStateToProps = (state) => ({
    company: state.company.company,
});

const mapDispatchToProps = (dispatch) => ({
    getCompany: (company) =>
        dispatch({
            type: ActionTypes.GET_COMPANY,
            company
        }),
    storeCompany: (company) =>
        dispatch({
            type: ActionTypes.STORE_COMPANY,
            company
        }),
    updateCompany: (company) =>
        dispatch({
            type: ActionTypes.UPDATE_COMPANY,
            company,
        })
});

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
