import * as ActionTypes from '../actions';

const initialState = {
    submissionCategory: [],
    detailSubmissionCategory: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_SUBMISSION_CATEGORY:
            return {
                ...state,
                submissionCategory: action.submissionCategory
            };
        case ActionTypes.STORE_SUBMISSION_CATEGORY: {
            const newSubmissionCategory = [...state.submissionCategory];
            newSubmissionCategory.splice(0, 0, action.detailSubmissionCategory);
            return {
                ...state,
                detailSubmissionCategory: action.detailSubmissionCategory,
                submissionCategory: newSubmissionCategory
            };
        }
        case ActionTypes.UPDATE_SUBMISSION_CATEGORY: {
            const updateSubmissionCategory = [...state.submissionCategory];
            updateSubmissionCategory.splice(action.index, 1);
            updateSubmissionCategory.splice(
                action.index,
                0,
                action.detailSubmissionCategory
            );
            return {
                ...state,
                detailSubmissionCategory: action.detailSubmissionCategory,
                submissionCategory: updateSubmissionCategory
            };
        }
        case ActionTypes.DELETE_SUBMISSION_CATEGORY: {
            const deleteSubmissionCategory = [...state.submissionCategory];
            deleteSubmissionCategory.splice(action.index, 1);
            return {
                ...state,
                submissionCategory: deleteSubmissionCategory
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
