import * as ActionTypes from '../actions';

const initialState = {
    productCategory: [],
    detailProductCategory: {}
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_PRODUCT_CATEGORY:
            return {
                ...state,
                productCategory: action.productCategory
            };
        case ActionTypes.STORE_PRODUCT_CATEGORY: {
            const newProductCategory = [...state.productCategory];
            newProductCategory.splice(0, 0, action.detailProductCategory);
            return {
                ...state,
                detailProductCategory: action.detailProductCategory,
                productCategory: newProductCategory
            };
        }
        case ActionTypes.UPDATE_PRODUCT_CATEGORY: {
            const updateProductCategory = [...state.productCategory];
            updateProductCategory.splice(action.index, 1);
            updateProductCategory.splice(
                action.index,
                0,
                action.detailProductCategory
            );
            return {
                ...state,
                detailProductCategory: action.detailProductCategory,
                productCategory: updateProductCategory
            };
        }
        case ActionTypes.DELETE_PRODUCT_CATEGORY: {
            const deleteProductCategory = [...state.productCategory];
            deleteProductCategory.splice(action.index, 1);
            return {
                ...state,
                productCategory: deleteProductCategory
            };
        }
        default:
            return {...state};
    }
};

export default reducer;
